.paper{
    background-color: transparent;
    border: outset;
    // padding: 1%;
    // margin: 1%;
    overflow: hidden;
    padding: 2%;
    height: #{"min(91vh, 91vw)"};
    font-size: #{"max(4vh, 4vw)"};
    color: white;
    text-align: center;
}

.fab {
    padding: 20px;
    font-size: 53px;
    width: 80px;
    height: 80px;
    text-align: center;
    text-decoration: none;
    margin: 5px 2px;
    border-radius: 50%;
}

.fab:hover {
    opacity: 0.7;
}

.fa-facebook {
    background: linear-gradient(to top, #0062e0, #19afff);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-size: 80px;
}

.fa-facebook-messenger {
    background-position: 20px 0px;
    background-image: linear-gradient(34deg, #0a7cff 22%, #a10eeb 44%, #ff5297 63%, #ff6666 66%);
    background: radial-gradient(circle at 20% bottom, #0a7cff 25%, #a10eeb 50%, #ff5297 64%, #ff6666 69%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-size: 80px;
}

.fa-skype {
    background: linear-gradient(to top, #007ddc, #00c8ff);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-size: 80px;
}

.fa-github {
    color: white;
    font-size: 80px;
}